import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Bar, Line, Pie } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineElement,
  ArcElement
} from 'chart.js';
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend, PointElement, LineElement, ArcElement);

const initialProducts = [
  { id: 1, name: "A", stock: 10, price: 1000, category: "" },
  { id: 2, name: "B", stock: 5, price: 2000, category: "" },
];

function Home() {
  const [products, setProducts] = useState(initialProducts);
  const [name, setName] = useState("");
  const [stock, setStock] = useState("");
  const [price, setPrice] = useState("");
  const [category, setCategory] = useState("");
  const [editId, setEditId] = useState(null);
  const [searchTerm, setSearchTerm] = useState("");
  const [categories, setCategories] = useState(["", ""]);
  const [newCategory, setNewCategory] = useState("");
  const [sortConfig, setSortConfig] = useState({ key: null, direction: null });
  const [searchCategory, setSearchCategory] = useState("");
  const [categoryToDelete, setCategoryToDelete] = useState(null);
  const [productToDelete, setProductToDelete] = useState(null);
  const [tab, setTab] = useState('main'); // 'main' or 'history'
  const [history, setHistory] = useState([]); // {id, type, productId, name, date, qty, price, total}

  // 
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!name || stock === "" || price === "" || !category || stock < 0 || price < 0) {
      alert("");
      return;
    }
    const newProduct = {
      id: editId ?? (products.length > 0 ? Math.max(...products.map((p) => p.id)) + 1 : 1),
      name,
      stock: Number(stock),
      price: Number(price),
      category,
    };
    if (editId === null) {
      setProducts([...products, newProduct]);
    } else {
      setProducts(products.map((p) => (p.id === editId ? newProduct : p)));
      setEditId(null);
    }
    setName("");
    setStock("");
    setPrice("");
    setCategory("");
  };

  // 
  const handleEdit = (product) => {
    setEditId(product.id);
    setName(product.name);
    setStock(product.stock);
    setPrice(product.price);
    setCategory(product.category);
  };

  // 
  const handleDelete = (id) => {
    setProducts(products.filter((p) => p.id !== id));
    if (editId === id) handleCancel();
    setProductToDelete(null);
  };

  // 
  const handleCancel = () => {
    setEditId(null);
    setName("");
    setStock("");
    setPrice("");
    setCategory("");
  };

  // 
  const handleDeleteCategory = (categoryToDelete) => {
    setCategories(categories.filter((c) => c !== categoryToDelete));
    if (category === categoryToDelete) setCategory("");
    if (searchCategory === categoryToDelete) setSearchCategory("");
    setCategoryToDelete(null);
  };

  // 
  const filteredProducts = products.filter((p) =>
    p.name.toLowerCase().includes(searchTerm.toLowerCase()) &&
    (searchCategory ? p.category === searchCategory : true)
  );
  const sortedProducts = [...filteredProducts];
  if (sortConfig.key !== null) {
    sortedProducts.sort((a, b) => {
      const aVal = a[sortConfig.key];
      const bVal = b[sortConfig.key];
      if (typeof aVal === "string") {
        return sortConfig.direction === "asc"
          ? aVal.localeCompare(bVal)
          : bVal.localeCompare(aVal);
      } else {
        return sortConfig.direction === "asc" ? aVal - bVal : bVal - aVal;
      }
    });
  }
  const handleSort = (key) => {
    setSortConfig((prev) => {
      if (prev.key === key) {
        if (prev.direction === "asc") return { key, direction: "desc" };
        if (prev.direction === "desc") return { key: null, direction: null };
      }
      return { key, direction: "asc" };
    });
  };

  // CSV
  const exportToCSV = () => {
    const header = ["ID", "", "", "", ""];
    const rows = sortedProducts.map((p) => [p.id, p.name, p.stock, p.price, p.category]);
    const csvContent = [header, ...rows].map((row) => row.join(",")).join("\n");
    // BOM
    const bom = new Uint8Array([0xEF, 0xBB, 0xBF]);
    const blob = new Blob([bom, csvContent], { type: "text/csv;charset=utf-8;" });
    const downloadLink = document.createElement("a");
    downloadLink.href = URL.createObjectURL(blob);
    downloadLink.download = `products.csv`;
    downloadLink.click();
  };

  // 
  const handleStockAction = (product, type) => {
    const qty = Number(prompt(`${type === 'in' ? '' : ''}`));
    if (!qty || qty <= 0) return;
    let newStock = product.stock;
    if (type === 'in') newStock += qty;
    if (type === 'out') {
      if (qty > product.stock) return alert('');
      newStock -= qty;
    }
    setProducts(products.map(p => p.id === product.id ? { ...p, stock: newStock } : p));
    setHistory([
      ...history,
      {
        id: history.length > 0 ? Math.max(...history.map(h => h.id)) + 1 : 1,
        type,
        productId: product.id,
        name: product.name,
        date: new Date().toISOString().slice(0, 7), // YYYY-MM
        qty,
        price: product.price,
        total: qty * product.price
      }
    ]);
  };

  // 
  const months = Array.from(new Set(history.map(h => h.date))).sort();
  const monthlyIn = months.map(m => history.filter(h => h.date === m && h.type === 'in').reduce((sum, h) => sum + h.qty, 0));
  const monthlyOut = months.map(m => history.filter(h => h.date === m && h.type === 'out').reduce((sum, h) => sum + h.qty, 0));
  const monthlyOutTotal = months.map(m => history.filter(h => h.date === m && h.type === 'out').reduce((sum, h) => sum + h.total, 0));

  return (
    <div style={{ padding: 24, maxWidth: 900, margin: "0 auto" }}>
      <h1></h1>
      <div style={{ marginBottom: 16 }}>
        <button onClick={() => setTab('main')} style={{ marginRight: 8, background: tab === 'main' ? '#4CAF50' : '#eee', color: tab === 'main' ? 'white' : 'black' }}></button>
        <button onClick={() => setTab('history')} style={{ background: tab === 'history' ? '#4CAF50' : '#eee', color: tab === 'history' ? 'white' : 'black' }}></button>
        <Link to="/help" style={{ float: 'right', padding: "8px 16px", backgroundColor: "#4CAF50", color: "white", textDecoration: "none", borderRadius: "4px" }}>
          
        </Link>
      </div>
      {tab === 'main' && (
        <>
          <input
            type="text"
            placeholder=""
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            style={{ marginBottom: 16, padding: 4, width: "100%" }}
          />
          <div style={{ marginBottom: 16 }}>
            <select onChange={(e) => setSearchCategory(e.target.value)} value={searchCategory}>
              <option value=""></option>
              {categories.map((c) => (
                <option key={c} value={c}>{c}</option>
              ))}
            </select>
          </div>
          <div style={{ marginBottom: 20 }}>
            <h3></h3>
            <input
              type="text"
              placeholder=""
              value={newCategory}
              onChange={(e) => setNewCategory(e.target.value)}
            />
            <button
              type="button"
              onClick={() => {
                if (newCategory.trim() !== "" && !categories.includes(newCategory.trim())) {
                  setCategories([...categories, newCategory.trim()]);
                  setNewCategory("");
                }
              }}
            >
              
            </button>
            {newCategory && (
              <button onClick={() => setNewCategory("")} style={{ marginLeft: 8 }}>
                
              </button>
            )}
            <div style={{ marginTop: 12 }}>
              {categories.map((c) => (
                <span key={c} style={{ display: "inline-block", marginRight: 8, background: "#f0f0f0", padding: "2px 8px", borderRadius: 4 }}>
                  {c}
                  <button onClick={() => setCategoryToDelete(c)} style={{ marginLeft: 4, color: "red", border: "none", background: "transparent", cursor: "pointer" }}></button>
                </span>
              ))}
            </div>
            {/*  */}
            {categoryToDelete && (
              <div style={{ position: "fixed", top: 0, left: 0, width: "100vw", height: "100vh", background: "rgba(0,0,0,0.3)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
                <div style={{ background: "white", padding: 24, borderRadius: 8, boxShadow: "0 2px 8px rgba(0,0,0,0.2)" }}>
                  <p>{categoryToDelete}</p>
                  <button onClick={() => handleDeleteCategory(categoryToDelete)} style={{ marginRight: 12, background: "#d32f2f", color: "white", border: "none", padding: "6px 16px", borderRadius: 4 }}></button>
                  <button onClick={() => setCategoryToDelete(null)} style={{ background: "#aaa", color: "white", border: "none", padding: "6px 16px", borderRadius: 4 }}></button>
                </div>
              </div>
            )}
          </div>
          <form onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
            <input
              type="text"
              placeholder=""
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <input
              type="number"
              placeholder=""
              value={stock}
              onChange={(e) => setStock(e.target.value)}
            />
            <input
              type="number"
              placeholder=""
              value={price}
              onChange={(e) => setPrice(e.target.value)}
            />
            <select value={category} onChange={(e) => setCategory(e.target.value)}>
              <option value=""></option>
              {categories.map((c) => (
                <option key={c} value={c}>{c}</option>
              ))}
            </select>
            <button type="submit">{editId === null ? "" : ""}</button>
            {editId !== null && (
              <button type="button" onClick={handleCancel} style={{ marginLeft: 8 }}>
                
              </button>
            )}
          </form>
          <div style={{ marginBottom: 20 }}>
            <button onClick={exportToCSV}>CSV</button>
          </div>
          <table border="1" cellPadding="8" style={{ width: "100%" }}>
            <thead>
              <tr>
                <th onClick={() => handleSort("id")}>ID</th>
                <th onClick={() => handleSort("name")}></th>
                <th onClick={() => handleSort("stock")}></th>
                <th onClick={() => handleSort("price")}></th>
                <th onClick={() => handleSort("category")}></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {sortedProducts.map((p) => (
                <tr key={p.id}>
                  <td>{p.id}</td>
                  <td>{p.name}</td>
                  <td style={{ color: p.stock <= 3 ? "red" : "black" }}>{p.stock}</td>
                  <td>{p.price}</td>
                  <td>{p.category}</td>
                  <td>
                    <button onClick={() => handleEdit(p)}></button>
                    <button onClick={() => setProductToDelete(p.id)}></button>
                    <button onClick={() => handleStockAction(p, 'in')} style={{ marginLeft: 4 }}></button>
                    <button onClick={() => handleStockAction(p, 'out')} style={{ marginLeft: 4 }}></button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          {/*  */}
          {productToDelete !== null && (
            <div style={{ position: "fixed", top: 0, left: 0, width: "100vw", height: "100vh", background: "rgba(0,0,0,0.3)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
              <div style={{ background: "white", padding: 24, borderRadius: 8, boxShadow: "0 2px 8px rgba(0,0,0,0.2)" }}>
                <p></p>
                <button onClick={() => handleDelete(productToDelete)} style={{ marginRight: 12, background: "#d32f2f", color: "white", border: "none", padding: "6px 16px", borderRadius: 4 }}></button>
                <button onClick={() => setProductToDelete(null)} style={{ background: "#aaa", color: "white", border: "none", padding: "6px 16px", borderRadius: 4 }}></button>
              </div>
            </div>
          )}
        </>
      )}
      {tab === 'history' && (
        <div>
          <h2></h2>
          <Bar
            data={{
              labels: months,
              datasets: [
                { label: '', data: monthlyIn, backgroundColor: '#4CAF50' },
                { label: '', data: monthlyOut, backgroundColor: '#f44336' }
              ]
            }}
          />
          <h2 style={{ marginTop: 32 }}></h2>
          <Line
            data={{
              labels: months,
              datasets: products.map(p => ({
                label: p.name,
                data: months.map(m => history.filter(h => h.date === m && h.type === 'out' && h.productId === p.id).reduce((sum, h) => sum + h.qty, 0)),
                borderColor: '#' + Math.floor(Math.random()*16777215).toString(16),
                fill: false
              }))
            }}
          />
          <h2 style={{ marginTop: 32 }}></h2>
          <Pie
            data={{
              labels: months,
              datasets: [
                {
                  data: monthlyOutTotal,
                  backgroundColor: months.map((_, i) => `hsl(${i * 60}, 70%, 60%)`)
                }
              ]
            }}
          />
          <h2 style={{ marginTop: 32 }}></h2>
          <table border="1" cellPadding="6" style={{ width: '100%' }}>
            <thead>
              <tr>
                <th>ID</th><th></th><th></th><th></th><th></th><th></th><th></th>
              </tr>
            </thead>
            <tbody>
              {history.map(h => (
                <tr key={h.id}>
                  <td>{h.id}</td>
                  <td>{h.date}</td>
                  <td>{h.name}</td>
                  <td>{h.type === 'in' ? '' : ''}</td>
                  <td>{h.qty}</td>
                  <td>{h.price}</td>
                  <td>{h.total}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

export default Home; 